<template>
  <div class="home">
    <div class="search-panel">
      <el-input class="input" v-model="keyword" @keyup.enter.native="handleSearch" placeholder="请输入商品关键词"></el-input>
      <div class="search" @click="handleSearch">搜索</div>
    </div>
    <div class="filter">
      <div class="title">类目</div>
      <div class="item"
        :class="{ 'item-selected': item.isSelected }"
        v-for="(item, index) in typeList"
        :key="index"
        @click="chooseType(item, index)">
        {{item.name}}
      </div>
    </div>
    <div class="filter">
      <div class="title">排序</div>
      <div class="item" @click="handleSort(1)" :class="{ 'item-selected': sort === 1 }">
        按佣金{{sort === 1 ? order === 'asc' ? '(↑)' : '(↓)' : ''}}
      </div>
      <div class="item" @click="handleSort(2)" :class="{ 'item-selected': sort === 2 }">
        按价格{{sort === 2 ? order === 'asc' ? '(↑)' : '(↓)' : ''}}
      </div>
      <div class="item" @click="handleSort(3)" :class="{ 'item-selected': sort === 3 }">
        最新上架{{sort === 3 ? order === 'asc' ? '(↑)' : '(↓)' : ''}}
      </div>
    </div>
    <div class="goods-panel" v-loading="loading">
      <div class="item" v-for="(item, index) in dataArray" :key="index" @click="goDetail(item)">
        <img :src="item.albumPics">
        <div class="title" :title="item.name">{{item.name}}</div>
        <div class="value">
          <div class="left" :title="item.price + '元'">{{item.price}}元</div>
          <div class="right" :title="item.rebateRate + '%'">{{item.rebateRate}}%</div>
        </div>
        <div class="label">
          <div class="left">券后价</div>
          <div class="right">佣金率</div>
        </div>
      </div>
    </div>
    <el-pagination
      background
      layout="->, prev, pager, next"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
import { fetchProductList4Home, searchProductList, fetchProductType } from '@/api/tkpro'
export default {
  name: 'home',
  data() {
    return {
      keyword: '',
      loading: false,
      currentPage: 1,
      pageSize: 10,
      total: 0,
      productCategoryId: 0,
      dataArray: [],
      typeList: [],
      sort: 0,
      order: 'asc',
    }
  },
  created(){
    this.currentPage = 1
    this.loadType()
    this.loadData()
  },
  methods:{
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.loadData()
      console.log(`当前页: ${val}`);
    },
    loadType () {
      fetchProductType().then(res => {
        if (res.code !== 200) return this.$message.error(res.message);
        this.typeList = res.data.map(item => {
          item.isSelected = false
          return item
        })
        console.log(JSON.parse(JSON.stringify(this.typeList)));
      }).catch(error => {
        console.log(error);
      })
    },
    loadData () {
      this.loading = true
      const productCategoryId = this.productCategoryId === 0 ? '' : this.productCategoryId
      const sort = this.sort === 0 ? '' : this.sort
      fetchProductList4Home(productCategoryId, this.keyWord, this.order, sort, this.currentPage).then(res => {
        this.dataArray = res.data.list
        this.total = res.data.total
        // this.pageSize = res.data.pageSize
        this.loading = false
      }).catch(error => {
        console.log(error);
        this.loading = false
      })
    },
    handleSearch () {
      this.productCategoryId = 0
      this.typeList.forEach(item => item.isSelected = false) // 清空类目选中状态
      this.sort = -1
      this.order = -1 // 清空排序选中状态
      this.dataArray = []
      searchProductList(this.keyword).then(res => {
        if (res.code !== 200) return this.$message.error(res.message);
        this.dataArray = res.data
        this.total = this.dataArray.length
        // this.pageSize = this.dataArray.length
        this.loading = false
      }).catch(error => {
        console.log(error);
        this.loading = false
      })
    },
    chooseType (item, aIndex) {
      console.log(aIndex);
      this.keyword = '' // 清空搜索框
      this.sort = -1
      this.order = -1 // 清空排序选中状态
      this.typeList.forEach((item, index) => {
        item.isSelected = index === aIndex
      })
      this.productCategoryId = item.id
      this.loadData()
    },
    handleSort (index) {
      this.keyword = '' // 清空搜索框
      this.productCategoryId = 0
      this.typeList.forEach(item => item.isSelected = false) // 清空类目选中状态
      if (this.sort === index) this.order = this.order === 'asc' ? 'desc' : 'asc'
      this.sort = index
      this.loadData()
    },
    goDetail (item) {
      this.$router.push({path:`/detail`,query: {id: item.id}});
    },
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.home {
  background: #eeeeee;
  padding: 20px 0;
  .search-panel {
    display: flex;
    width: 641px;
    align-items: center;
    height: 55px;
    background-color: #fff;
    margin: 0 auto;
    padding: 10px 24px 14px 21px;
    margin-bottom: 20px;
    .input {
      width: 548px;
      height: 35px;
      border-radius: 53px;
      background-color: rgba(247, 247, 247, 100);
      color: rgba(223, 219, 219, 100);
      font-size: 13px;
    }
    .search {
      color: #0084FF;
      margin-left: 20px;
      font-size: 14px;
      cursor: pointer;
    }
  }
  .filter {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    background-color: rgba(255, 255, 255, 100);
    color: rgba(16, 16, 16, 100);
    font-size: 14px;
    text-align: center;
    padding: 20px 35px;
    color: rgba(16, 16, 16, 100);
    font-size: 14px;
    .title {
      margin-right: 37px;
      font-weight: bold;
    }
    .item {
      margin-right: 21px;
      cursor: pointer
    }
    .item-selected {
      color: #031FBB;
      font-weight: bold;
    }
  }
  .sort {
    display: flex;
    align-items: center;
    margin: 11px 0;
  }
  .goods-panel {
    padding: 10px;
    margin-bottom: 24px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    .item {
      display: flex;
      padding: 17px 25px;
      flex-direction: column;
      background: #fff;
      margin: 12px 35px;
      cursor: pointer;
      img {
        width: 168px;
        height: 168px;
        margin-bottom: 9px;
      }
      .title {
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        width: 168px;
        color: rgba(16, 16, 16, 100);
        font-size: 14px;
        margin-bottom: 16px;
      }
      .value {
        display: flex;
        flex-direction: row;
        margin-bottom: 7px;
        justify-content: space-around;
        .left {
          color: rgba(16, 16, 16, 100);
          font-size: 16px;
          font-weight: bold;
        }
        .right {
          color: rgba(255, 17, 17, 100);
          font-size: 16px;
          font-weight: bold;
        }
      }
      .label {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        .left {
          color: rgba(146, 146, 146, 100);
          font-size: 14px;
        }
        .right {
          color: rgba(146, 146, 146, 100);
          font-size: 14px;
        }
      }
    }
  }
}
</style>



